.slick-slider .slick-track, .slick-slider .slick-list {
    transform: translate3d(0, 0, 0);
    height: 45vh;
}
.slick-slide img {
    display: block;
    
    max-width:100%; max-height:100%; width:100%; height:100%;
}
.slick-dots {
    bottom: 45px;
    li.slick-active button:before {
        color: #f6f6f6;
    }
    .slick-active button:before {
        color: #f6f6f6;
    }
}